.complete {
    @include middle();
    width: 100%;
}

.complete-bubbles {
    @include middle();
    background-image: url("../img/bubbles-complete.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 380px;
    width: 100%;
}

.complete-icons {
    background-size: cover;
    background-repeat: no-repeat;
    height: 212px;
    margin: 0 auto;
    position: relative;
}

.complete-icons-top {
    background-image: url("../img/icons-complete-top.png");
    width: 385px;
}

.complete-icons-bottom {
    background-image: url("../img/icons-complete-bottom.png");
    width: 380px;
}

.complete-text {
    @include font-size(37);
    padding: 15px 0;
    display: block;
    text-align: center;
    max-width: 520px;
    margin: 0 auto;
}

@media (max-width: $breakpoint-mobile) {

    .complete-icons-top {
        width: 280px;
        height: 154px;
    }

    .complete-icons-bottom {
        width: 280px;
        height: 155px;
    }

    .complete-text {
        @include font-size(23);
    }

}