@mixin keyframes($animation-name) {
  @-webkit-keyframes #{$animation-name} {
    @content;
  }
  @-moz-keyframes #{$animation-name} {
    @content;
  }
  @-ms-keyframes #{$animation-name} {
    @content;
  }
  @-o-keyframes #{$animation-name} {
    @content;
  }
  @keyframes #{$animation-name} {
    @content;
  }
}

@mixin animation($str) {
  -webkit-animation: #{$str};
  -moz-animation: #{$str};
  -ms-animation: #{$str};
  -o-animation: #{$str};
  animation: #{$str};
}


// Font size (rem + px)
$base-font-size-for-rem-calc: 16;
@mixin rem-polyfill($desired-rem-size) {
    font-size: ($desired-rem-size * $base-font-size-for-rem-calc) + px;
    font-size: $desired-rem-size + rem;
}

@mixin font-size($desired-pixel-size) {
    $desired-rem-size: $desired-pixel-size / $base-font-size-for-rem-calc;
    font-size: ($desired-rem-size * $base-font-size-for-rem-calc) + px;
    font-size: $desired-rem-size + rem;
}

// Position center and middle of parent
@mixin center-middle() {
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
}

// Position middle of parent
@mixin middle() {
    top: 50%;
    position: absolute;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

// Most frame sizes
@mixin frame() {
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 15px;
}