.welcome {
    padding: 35px 0;
}

.welcome-title {
    @include frame();
    color: white;
    margin-bottom: 50px;
    h1 {
        padding-bottom: 20px;
        line-height: 1.4;
        @include font-size(29);
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    }
}

.welcome-content {
    margin: 0 auto;
    text-align: center;
    position: relative;
}

.welcome-bubbles {
    @include middle();
    background-image: url("../img/bubbles-welcome.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 380px;
    width: 100%;
}

.welcome-icons {
    background-image: url("../img/icons-welcome-desktop.png");
    background-size: cover;
    background-repeat: no-repeat;
    width: 530px;
    height: 570px;
    margin: 0 auto;
    position: relative;
}

@media (max-width: $breakpoint-mobile) {

    .welcome {
        padding: 15px 0;
    }

    .welcome-title h1 {
        @include font-size(19);
    }

    .welcome-icons {
        background-image: url("../img/icons-welcome-mobile.png");
        width: 280px;
        height: 390px;
    }

}