.frame {
    padding-top: 80px;
    position: relative;
    width: 100%;
    height: 100%;
    &.frame-red {
        background: #835365;
    }
    &.frame-green {
        background: #1E8978;
    }
    &.frame-blue {
        background: #0F769F;
    }
    &.frame-greyish {
        background: #628788;
    }
    &.frame-brightgreen {
        background: #15A674;
    }
}