.intro-wrap {
    @include middle();
    width: 100%;
}

.intro {
    max-width: 670px;
    margin: 0 auto;
    padding: 0 15px;
    text-align: center;
}

.intro-title {
    width: 100%;
    color: white;
    margin-bottom: 30px;
    h1 {
        display: inline-block;
        padding-bottom: 8px;
        @include font-size(23);
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    }
}

.intro-heading {
    width: 100%;
    display: block;
    @include font-size(47);
    margin-bottom: 15px;
}

.intro-icons {
    background-image: url("../img/icons-intro-desktop.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100%;
    width: 100%;
    height: 74px;
    margin-bottom: 50px;
}

@media (max-width: 720px) {

    .intro {
        max-width: 280px;
    }

    .intro-heading {
        @include font-size(29);
    }

    .intro-icons {
        background-image: url("../img/icons-intro-mobile.png");
        background-size: 100%;
        height: 135px;
    }

}