$pl-black: #000001;
$pl-dark-grey: #515556;
$pl-cream: #d9d1c3;
$pl-grey: #c7cbcc;
$pl-ground: #c5c2be;

$primary-green1: #00b287;
$primary-green2: #3cc3b2;
$primary-green3: #007178;
$primary-green4: #76bd1d;
$primary-green5: #bfd09a;
$primary-green6: #91c99e;
$primary-orange1: #ff9016;
$primary-orange2: #f6a704;
$primary-orange3: #d7b98a;
$primary-yellow1: #ffe00a;
$primary-yellow2: #fdf0c4;
$primary-blue1: #0b1e60;
$primary-blue2: #00acc8;
$primary-blue3: #62ccca;
$primary-blue4: #87d2e7;
$primary-blue5: #a1d7e3;


$secondary-green1: #036b5a;
