$skin3: #bf955a;
$skin4: #f5d09e;

@mixin define-colour($colour, $text_colour){
  color: $colour;
  &.bg {
    background-color: $colour;
    color: $text_colour;
  }
  &.border {
    border-color: $colour;
    color: black;
  }
  &.border.bg {
    color: $text_colour;
  }
}

.pl-red { @include define-colour($pl-red, white); }
.pl-black { @include define-colour($pl-black, white); }
.pl-dark-grey { @include define-colour($pl-dark-grey, white); }
.pl-cream { @include define-colour($pl-cream, $pl-black); }
.pl-grey { @include define-colour($pl-grey, $pl-black); }
.pl-ground { @include define-colour($pl-ground, $pl-black); }

.primary-green1 { @include define-colour($primary-green1, white); }
.primary-green2 { @include define-colour($primary-green2, black); }
.primary-green3 { @include define-colour($primary-green3, white); }
.primary-green4 { @include define-colour($primary-green4, white); }
.primary-green5 { @include define-colour($primary-green5, black); }
.primary-green6 { @include define-colour($primary-green6, black); }
.primary-orange1 { @include define-colour($primary-orange1, white); }
.primary-orange2 { @include define-colour($primary-orange2, white); }
.primary-orange3 { @include define-colour($primary-orange3, black); }
.primary-yellow1 { @include define-colour($primary-yellow1, black); }
.primary-yellow2 { @include define-colour($primary-yellow2, black); }
.primary-blue1 { @include define-colour($primary-blue1, white); }
.primary-blue2 { @include define-colour($primary-blue2, white); }
.primary-blue3 { @include define-colour($primary-blue3, black); }
.primary-blue4 { @include define-colour($primary-blue4, black); }
.primary-blue5 { @include define-colour($primary-blue5, black); }

.secondary-green1 { @include define-colour($secondary-green1, white); }
