a.button {
    background: none;
    border: none;
    border-radius: 50px;
    letter-spacing: 1px;
    color: white;
    display: inline-block;
    text-decoration: none;
    text-align: center;
    transition: $transition;
    position: relative;
    height: 45px;
    line-height: 43px;
    width: 160px;
    @include font-size(23);
    .mdi-play {
        transition: $transition;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        right: 12px;
        @include font-size(30);
    }
}

a.button-logout {
    @include font-size(12);
    height: 22px;
    line-height: 22px;
    width: 90px;
    background: rgba(0, 0, 0, 0.35);
    &:hover {
        background: rgba(0, 0, 0, 0.5);
    }
}

a.button-logout-mobile {
    width: 100%;
    border-radius: 5px;
    margin-top: 8px;
    background: #FF005E;
    &:hover {
        background: darken(#FF005E, 5);
    }
}

a.button-start {
    @include center-middle();
    background: #FF005E;
    &:hover {
        background: darken(#FF005E, 5);
    }

}

a.button-intro {
    position: relative;
    background: white;
    color: #404344;
    &:hover {
        color: #FF005E;
        .mdi-play {
            color: #FF005E;
        }
    }
}

a.button-next {
    position: relative;
    background: rgba(255, 255, 255, 0.3);
    width: 190px;
    color: white;
    float: left;
    margin-left: 15px;
    &.selected {
        background: rgba(255, 255, 255, 0.9);
        color: #FF005E;
        .mdi-play {
            color: #FF005E;
        }
    }
    &:hover {
        background: #FF005E;;
        color: white;
        .mdi-play {
            color: white;
        }
    }
}

a.button-back {
    position: relative;
    background: rgba(255, 255, 255, 0.3);
    height: 45px;
    width: 45px;
    float: left;
    text-align: center;
    &:hover {
        background: #FF005E;;
        color: white;
    }
    .mdi-play {
        right: 18px;
        top: 8px;
        transform: rotate(180deg);
    }
}