.pl-logo {

  font-size: 1em;

  &.colour,
  &.mono {

    position: relative;
    height: 1em;
    width: 3.3em;
    color: black;
    display: inline-block;

    .symbol {

      position: absolute;
      top: 0;
      left: 0;

      &:before,
      &:after,
      span:before {
        font-family: "pllogo";
        font-style: normal;
        font-weight: normal;
        speak: none;
        display: inline-block;
        text-decoration: inherit;
        width: 1em;
        margin: 0;
        text-align: center;
        font-variant: normal;
        text-transform: none;
        line-height: 1em;

        position: absolute;
        top: 0;
        left: 0;
      }


      &:before {
        content: '\e804';
        color: #701516;
      }

      span:before {
        content: '\e803';
        color: #a51f1e;
      }

      &:after {
        content: '\e802';
        color: #e31d1d;
      }
    }
    .text {

      font-size: 1em;
      font-style: normal;

      position: absolute;
      top: 0;
      left: 1.1em;

      &:before,
      &:after {
        font-size: 0.35em;
        position: absolute;
        left: 0;
      }

      &:before {
        font-family: 'Sanchez Slab';
        content: 'Performance';
        font-weight: 500 !important;
        top: 0.4em;
      }
      &:after {
        font-family: 'Sanchez Slab';
        font-weight: 200 !important;
        content: 'Learning';
        top: 1.4em;
      }

    }

    .r {
      font-size: 0.3em;
      font-style: normal;
      position: absolute;
      top: 1.75em;
      left: 8.7em;
    }

    &.white {
      .text {
        color: white;
      }
    }
  }

  &.mono {
    &.white {
      .symbol:before,
      .symbol:after,
      .symbol span:before,
      .text {
        color: white;
      }
    }
    &.black {
      .symbol:before,
      .symbol:after,
      .symbol span:before,
      .text {
        color: black;
      }
    }

    .symbol:before,
    .symbol:after,
    .symbol span:before {
      opacity: 0.6;
    }
  }


  &.gradient {
    position: relative;
    height: 1em;
    width: 3.3em;
    color: black;
    display: inline-block;
    background: transparent url(../img/PL_Logo_Text.png);
    background-size: cover;
  }

}


.pl-loader {
  @extend .pl-logo.mono;
  width: 1em;
  font-size: 4em;
  text-align: center;

  .symbol {
    &:before,
    &:after,
    span:before {
      color: transparent;
    }


    &:after {
      @include animation('pulse1 2s infinite');
    }
    span:before {
      @include animation('pulse2 2s infinite');
    }
    &:before {
      @include animation('pulse3 2s infinite');
    }
  }

  .message {
    font-size: 0.2em;
    font-style: normal;
    position: absolute;
    text-align: center;
    width: 10em;
    display: inline-block;
    top: 6em;
    left: -2.5em;
  }

  &.loading {
    .symbol {
      span:after {
        content: 'Loading';
        font-size: 0.2em;
        font-style: normal;
        top: 2.2em;
        position: relative;
        text-align: center;
        width: 5em;
        display: inline-block;
      }
    }
  }
}

@include keyframes(pulse1) {
  0% {    color: transparent;  }
  35% {   color: $pl-red;  }
  70% {  color: transparent;  }
  100% {  color: transparent;  }
}

@include keyframes(pulse2) {
  0% {    color: transparent;  }
  10% {   color: transparent;  }
  45% {   color: $pl-red;      }
  80% {   color: transparent;  }
  100% {  color: transparent;  }
}

@include keyframes(pulse3) {
  0% {    color: transparent;  }
  20% {   color: transparent;  }
  55% {   color: $pl-red;      }
  90% {   color: transparent;  }
  100% {  color: transparent;  }
}

