.header {
    width: 100%;
    height: 80px;
    padding: 0 15px;
    position: fixed;
    z-index: 20;
    background: rgba(253, 251, 196, 0.15);
}

.header-logo {
    width: 169px;
    height: 51px;
    margin-top: 15px;
    float: left;
}

.header-control {
    position: relative;
    float: right;
    text-align: right;
}

.header-desktop {
    display: block;
}

.header-mobile {
    display: none;
    &.open .header-icon.zmdi {
        color: #666;
    }
    &.open .header-control-reveal {
        visibility: visible;
        opacity: 1;
    }
}

.header-control-reveal {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s linear;
    background: white;
    position: absolute;
    right: -8px;
    top: 15px;
    width: 220px;
    border-radius: 8px;
    padding: 8px;
    padding-top: 0;
    color: #666;
    .header-user {
        padding-right: 46px;
        line-height: 1.3;
        margin-top: 8px;
        strong {
            display: block;
        }
    }
}

.header-user {
    margin-top: 15px;
    display: block;
    @include font-size(13);
}

.header-icon.zmdi {
    line-height: 80px;
    @include font-size(36);
    position: relative;
    z-index: 20;
    &:hover {
        cursor: pointer;
    }
}

@media (max-width: $breakpoint-mobile) {

    .header-desktop {
        display: none;
    }

    .header-mobile {
        display: block;
    }

}