.assessment {
    padding: 35px 0 0 0;
}

.assessment-title {
    @include frame();
    color: white;
    margin-bottom: 50px;
    h1 {
        padding-bottom: 20px;
        line-height: 1.4;
        @include font-size(29);
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
        padding-left: 0;
    }
    img {
        display: none;
        float: none;
    }
}

.assessment-content {
    @include frame();
    margin: 0 auto 30px auto;
}

.assessment-questions {
    float: left;
    width: 63%;
    li {
        position: relative;
        padding: 15px 15px 15px 0;
        background: rgba(0, 0, 0, 0.1);
        border-radius: 5px;
        margin-bottom: 15px;
        @include font-size(23);
        line-height: 1.4;
        transition: $transition;
        &:hover {
            background: rgba(0, 0, 0, 0.4);
            cursor: pointer;
        }
        &.selected {
            background: rgba(0, 0, 0, 0.4);
        }
    }
}

.assessment-order {
    position: absolute;
    height: 100%;
    width: 80px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-transform: uppercase;
    span {
        @include center-middle();
        @include font-size(36);
        font-family: 'raleway-light'
    }
}

.assessment-text {
    padding-left: 80px;
    display: block;
}

.assessment-icon {
    float: right;
    width: 31%;
    img {
        width: 100%;
    }
}

.assessment-control {
    @include frame();
}


@media (max-width: 890px) {

    .assessment-title {
        h1 {
            padding-left: 90px;
        }
        img {
            display: block;
            width: 70px;
            float: left;
            position: relative;
            top: 5px;
        }
    }

    .assessment-questions {
        float: none;
        width: 100%;
    }

    .assessment-icon {
        display: none;
    }

}

@media (max-width: $breakpoint-mobile) {

    .assessment {
        padding: 15px 0;
    }

    .assessment-title {
        h1 {
            @include font-size(19);
            padding-left: 65px;
        }
        img {
            width: 50px;
        }
    }

    .assessment-questions li {
        @include font-size(17);
    }

    .assessment-control {
        display: none;
    }

}