body, input, select, textarea, button {
    font-family: 'raleway-regular', 'Arial', 'Helvetica', 'san-serif';
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -o-font-smoothing: antialiased;
    font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    line-height: 1.6;
    font-weight: 200;
    color: white;
}

.primary {
    .welcome-title h1,
    .assessment-title h1,
    .intro-heading,
    .assessment-text,
    .complete-text {
        font-family: 'short-stack';
    }
}

a, a:visited {
    //
}
p {
    margin: 0;
    padding: 0;
    margin-bottom: 1.4em;
}

strong {
    font-family: 'raleway-bold';
    font-weight: 600;
}

.text-small {
    font-size: 0.7em;
}

ul, ol {
    margin-bottom: 1.4em;
}

li {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}