.progress {
    width: 100%;
    position: absolute;
    bottom: 0;
    height: 50px;
    background: rgba(0, 0, 0, 0.3);
}

.progress-amount {
    height: 50px;
    background: #FF005E;
    position: relative;
    &.progress-over50 span {
        right: 30px;
    }
    span {
        position: absolute;
        @include font-size(19);
        line-height: 50px;
        right: -150px;
    }
}

@media (max-width: $breakpoint-mobile) {

    .progress {
        height: 20px;
    }

    .progress-amount {
        height: 20px;
        span {
            display: none;
        }
    }

}